@import "../../initialStyle";
footer {
  box-sizing: border-box;
  padding: 30px 0;
  text-align: center;
  box-shadow: 0 -1px 8px -2px rgba(0, 0, 0, 0.2);
  background-color: #ffffff;

  p {
    font-size: $h6;
    color: $color-4;
    &:last-child {
      padding-top: 0.5rem;
    }
  }
}
